.section4__container {
  padding: 60px 20px;
  text-align: center;
}

.section4__subheader {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
}

.section4__subheader h1 {
  margin: 0;
  color: #555;
}

.section4__header {
  font-size: 1.375rem;
  margin-bottom: 40px;
  color: #444;
  font-style: italic;
}

.room__card {
  /* background: white; */
  border-radius: 8px;
  /* box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15); */
  overflow: hidden;
  transition: transform 0.3s, box-shadow 0.3s;
}

.room__card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
}

.room__card__image {
  position: relative;
}

.room__card__icons {
  position: absolute;
  bottom: 10px;
  right: 10px;
  display: flex;
  gap: 10px;
}

.room__card__icons span:first-child {
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.9);
  color: #f472b6;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  font-size: 20px;
  padding: 10px;
  transition: background 0.3s, transform 0.3s;
}

.room__card__icons span:nth-child(2) {
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.9);
  color: #c084fc;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  font-size: 20px;
  padding: 10px;
  transition: background 0.3s, transform 0.3s;
}

.room__card__icons span:nth-child(3) {
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.9);
  color: #60a5fa;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  font-size: 20px;
  padding: 10px;
  transition: background 0.3s, transform 0.3s;
}

.room__card__icons span:hover {
  background: rgba(255, 255, 255, 1);
  transform: scale(1.1);
}

.room__card__image img {
  width: 100%;
  height: auto;
  display: block;
  border-bottom: 4px solid #007bff;
}

.room__card__details {
  padding: 30px;
}

.room__card__details h3 {
  font-size: 1.125rem;
  margin: 10px 0;
  color: #333;
}

.room__card__details p {
  font-size: 0.875rem;
  color: #666;
}

.room__card__details h5 {
  font-size: 1rem;
  color: #007bff;
  margin: 10px 0;
}

.btn {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  width: 30%;
  transition: background-color 0.3s, transform 0.3s;
}

.btn:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.btn:focus {
  outline: 2px solid #0056b3;
  outline-offset: 2px;
}

.carousel-controls {
  margin-top: 20px;
}

.carousel-controls button {
  padding: 10px 15px;
  margin: 0 5px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
}

.carousel-controls button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

@media (max-width: 1024px) {
  .room__card {
    margin: 10px;
    flex: 1 1 calc(33.333% - 20px);
  }

  h2 {
    font-size: 1.375rem;
  }

  .section4__header {
    font-size: 1.25rem;
  }
}

@media (max-width: 768px) {
  .room__card {
    flex: 1 1 calc(50% - 20px);
    margin: 0;
  }

  h1 {
    font-size: 2rem;
  }

  .section4__header {
    font-size: 2rem;
    margin-bottom: 10px;
  }

  .btn {
    padding: 8px 0px;
    font-size: 0.7rem;
    width: 100%;
    max-width: 100px;
    margin-top: 10px;
    display: inline-block;
  }
}

@media (max-width: 480px) {
  .section4__container {
    padding: 1rem;
  }
  .room__card {
    flex: 1 1 100%;
    margin: 0px;
  }

  .room__card__details {
    padding-bottom: 0;
  }

  .carousel-controls {
    margin: 0;
    padding-top: 2rem;
  }

  h1 {
    font-size: 1rem;
  }

  .section4__header {
    font-size: 0.8rem;
  }

  .btn {
    padding: 8px 0px;
    font-size: 0.7rem;
    width: 100%;
    max-width: 100px;
    margin-top: 10px;
    display: inline-block;
  }
}
