.about_content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: auto;
  margin: 0;
}

.line {
  width: 6.25rem;
  height: 0.125rem;
  background-color: blue;
  margin: 0 0.625rem;
}

.section7__subheader {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
}

.section7__subheader h1 {
  margin: 0;
  color: #555;
}

.container11 {
  display: grid;
  grid-template-columns: repeat(5, 2fr);
  grid-template-rows: repeat(3, 100px);
  gap: 2px;
  padding: 0 0.5rem;
}

.box11 {
  overflow: hidden;
  position: relative;
}

.box11:first-child {
  grid-column: 1;
  grid-row: 1 / span 2;
}

.box11:nth-child(2) {
  grid-column: 2 / span 2;
  grid-row: 1;
}

.box11:nth-child(6) {
  grid-column: 3 / span 2;
  grid-row: 2;
}

.box11:nth-child(7) {
  grid-column: 5;
  grid-row: 2;
}

.box11:nth-child(9) {
  grid-column: 2 / span 2;
  grid-row: 3;
}

.box11 img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* max-height: auto; */
  display: block;
  transition: transform 0.3s ease;
}

.box11:hover img {
  transform: scale(1.05);
}

@media (max-width: 768px) {
  .section7__subheader {
    font-size: 38px;
  }
  .line {
    width: 7rem;
    height: 0.08rem;
    background-color: blue;
    margin: 0 0.625rem;
  }
}

@media (max-width: 480px) {
  .section7__subheader {
    font-size: 1.5rem;
  }

  .line {
    width: 2.5rem;
    height: 0.06rem;
    background-color: blue;
    margin: 0 0.625rem;
  }
}
