.section6__container {
  padding: 2rem 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.banner__content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
}

.banner__card {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  background-color: #ffffff;
  border-radius: 20px;
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.2);
  padding: 40px;
  text-align: center;
  flex: 1;
  min-width: 200px;
  margin: 0 1rem;
}

.banner__card h1,
.banner__card h4 {
  margin: 0;
  font-size: 3rem; 
  color: #007bff;
}

.banner__card p {
  margin-top: 10px;
  font-size: 1.5rem; 
  color: #555;
}

@media (max-width: 1024px) {
  .section6__container {
    padding: 2rem 3rem;
  }

  .banner__card {
    margin: 1rem;
  }

  .banner__card h1,
  .banner__card h4 {
    font-size: 2.5rem; 
  }

  .banner__card p {
    font-size: 1.2rem; 
  }
}

@media (max-width: 768px) {
  .section6__container {
    padding: 1rem; 
    flex-direction: column; 
  }

  .banner__card {
    width: 100%;
    margin: 1rem 0; 
  }

  .banner__card h1,
  .banner__card h4 {
    font-size: 2rem; 
  }

  .banner__card p {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .section6__container {
    padding-top: 0;
    padding-bottom: 0;
  }

  .banner__card {
    padding: 1rem;
  }

  .banner__card h1,
  h4 {
    font-size: 2rem;
  }
}
