.section2__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  background-color: rgba(255, 255, 255, 0.8);
  margin-bottom: 2rem;
}

.booking__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input__group {
  margin: 0;
  width: 70vw;
  max-width: 180vw;
  display: flex;
  justify-content: center; 
  align-items: center;
  padding: 1rem;
  gap: 2rem;
  border-radius: 0 3rem 0 3rem;
  background: rgba(16, 30, 189, 0.31);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(3.4px);
  -webkit-backdrop-filter: blur(3.1px);
  border: 1px solid rgba(16, 30, 189, 0.25);
}

.checkIn,
.checkOut {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: left;
  color: blue;
  flex: 1; 
}

.input__group label {
  margin-bottom: 0.1rem; 
  font-weight: bold; 
}

.input__group div {
  flex: 1; 
  margin: 0 0.5rem;
  max-width: 150px; 
}

.input__group input {
  width: 80%; 
  padding: 0.5rem; 
  border: 1px solid #ccc; 
  border-radius: 5px; 
}

.section2__btn {
  width: 100%; 
  color: black;
  background-color: rgb(11, 32, 230);
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  outline: none;
  border-radius: 5px;
  border: none; 
  padding: 0.75rem;
  transition: background-color 0.3s ease;
  margin-top: 1rem;
  max-width: 300px; 
}

.section2__btn:hover {
  background-color: rgb(7, 7, 12);
  color: white;
}

@media (max-width: 768px) {
  .input__group {
    width: 70vw;
  }
  .input__group label {
    font-size: 0.7rem;
  }
  .input__group {
    padding: 1rem;
    gap: 1rem;
  }
}

@media (max-width: 480px) {
  .input__group {
    padding: 0.5rem;
    gap: 0;
    width: 100vw;
    border-radius: 0;
  }

  .input__group label {
    font-size: 0.8rem;
  }

  .input__group div {
    margin: 0; 
    margin-bottom: 1rem; 
  }

  .section2__btn {
    padding: 0.6rem; 
    font-size: 0.6rem; 
  }

  .input__group input {
    font-size: 0.7rem; 
    width: 70%;
    padding: 0.6rem;
  }
}
