.footer {
  background-color: skyblue;
  color: #101111;
  padding: 0 1rem;
  border-top: 2px solid #e9ecef;
}

.footer__container {
  display: flex;
  justify-content: space-between;
  max-width: 1200px;
  margin: 5rem auto;
}

.footer__col {
  flex: 1;
  margin: 0 1rem;
}

.logo h4 {
  font-size: 1.6rem;
  font-weight: bold;
  margin: 0;
  color: black;
}

.section__description {
  margin: 1rem 0;
  font-size: 0.9rem;
  line-height: 1.5;
}

.footer__links {
  list-style: none;
  padding: 0;
}

.footer__links li {
  margin: 0.5rem 0;
}

.footer__links a {
  color: #007bff;
  text-decoration: none;
  transition: color 0.3s;
}

.footer__links a:hover {
  color: #0056b3;
}

.footer__socials {
  display: flex;
  margin-top: 1rem;
}

.footer__socials a {
  margin-right: 15px;
}

.footer__socials img {
  width: 24px;
  height: 24px;
}

.footer__bar {
  background-color: #0a0a0a;
  color: #ffffff;
  text-align: center;
  padding: 1rem 0;
  font-size: 0.8rem;
}

@media (max-width: 768px) {
  .footer__container {
    flex-direction: column;
    align-items: center;
    margin: 2rem auto;
  }

  .footer__col {
    margin: 1rem 0;
    text-align: center;
  }

  .logo h4 {
    font-size: 1.4rem;
  }

  .section__description {
    font-size: 0.85rem;
    text-align: center;
  }

  .footer__links a {
    font-size: 0.85rem;
  }

  .footer__bar {
    font-size: 0.75rem;
  }
}

@media (max-width: 480px) {
  .footer__container {
    padding: 0 0.5rem;
    align-items: center;
  }

  .logo h4 {
    font-size: 1.2rem;
  }

  .section__description {
    font-size: 0.75rem;
    text-align: center;
  }

  .footer__links a {
    font-size: 0.75rem;
  }

  .footer__socials img {
    width: 20px;
    height: 20px;
  }
}
