.section8 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 7.5rem 2rem;
  min-height: 100vh;
  background: rgba(87, 176, 211, 0.342);
  background: white;
}

.container {
  display: flex;
  justify-content: space-between;
  z-index: 1000;
}

.container .contactInfo {
  width: 350px;
  background: rgba(135, 206, 250, 0.7);
  backdrop-filter: blur(10px);
  z-index: 1;
  padding: 4rem;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
  border-radius: 22px;
  position: relative;
}

.container .contactInfo::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("../assets/conatct\ info.jpg");
  background-size: cover;
  background-position: center;
  filter: blur(10px);
  z-index: -1;
  border-radius: 22px;
}

.container .contactInfo::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(135, 207, 235, 0.404);
  border-radius: 22px;
  z-index: -1;
}

.container .contactInfo h2 {
  color: black;
  font-size: 2.5rem;
  font-weight: 600;
  text-align: center;
}

.container .contactInfo ul.info {
  padding: 0;
  margin: 20px 0;
}

.container .contactInfo ul.info li {
  list-style: none;
  display: flex;
  margin: 20px 0;
  cursor: pointer;
}

.container .contactInfo ul.info li span:nth-child(1):hover {
  color: blue;
}

.container .contactInfo ul.info li span:nth-child(1) {
  width: 2rem;
  min-width: 30px;
}

.container .contactInfo ul.info li span a {
  color: #333;
  text-decoration: none;
}

.container .contactInfo ul.info li span a:hover {
  color: blue;
}

.container .contactInfo ul.info li span:nth-child(1) img {
  max-width: 100%;
  filter: invert(1);
}

.container .contactInfo ul.info li span:nth-child(1) img:hover {
  filter: invert(0);
}

.container .contactInfo ul.info li span:nth-child(2) {
  color: #333;
  margin-left: 10px;
  font-weight: 300;
}

.container .contactInfo ul.sci {
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  padding: 0;
}

.container .contactInfo ul.sci li {
  list-style: none;
  margin-right: 15px;
}

.container .contactInfo ul.sci li a {
  text-decoration: none;
}

.container .contactInfo ul.sci li a img {
  filter: invert(1);
}

.container .contactInfo ul.sci li a img:hover {
  filter: invert(0);
}

.container .contactForm {
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);
  box-shadow: 0 10px 50px rgba(0, 0, 0, 0.2);
  border-radius: 22px;
  flex: 1;
  margin-left: 20px;
  padding: 40px;
  position: relative;
}

.container .contactForm::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("../assets/conatct.jpg");
  background-size: cover;
  background-position: center;
  filter: blur(10px);
  z-index: -1;
}

.container .contactForm::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(135, 207, 235, 0.493);
  border-radius: 22px;
  z-index: -1;
}

.container .contactForm h2 {
  color: black;
  font-size: 2.5rem;
  text-align: center;
  font-weight: 600;
}

.container .contactForm .formBox {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-top: 30px;
}

.container .contactForm .formBox .inputBox {
  position: relative;
  margin-bottom: 35px;
}

.container .contactForm .formBox .inputBox.w50 {
  width: 35%;
}

.container .contactForm .formBox .inputBox.w100 {
  width: 100%;
}

.container .contactForm .formBox .inputBox input,
.container .contactForm .formBox .inputBox textarea {
  width: 100%;
  resize: none;
  padding: 5px 0;
  font-size: 18px;
  font-weight: 300;
  background: transparent;
  color: #333;
  border: none;
  outline: none;
  border-bottom: 1px solid #777;
}

.container .contactForm .formBox .inputBox textarea {
  height: 110px;
}

.container .contactForm .formBox .inputBox span {
  position: absolute;
  left: 0;
  padding: 5px 0;
  pointer-events: none;
  font-size: 18px;
  font-weight: 300;
  transition: 0.3s;
}

.container .contactForm .formBox .inputBox input:focus ~ span,
.container .contactForm .formBox .inputBox input:valid ~ span,
.container .contactForm .formBox .inputBox textarea:focus ~ span,
.container .contactForm .formBox .inputBox textarea:valid ~ span {
  transform: translateY(-25px);
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 1px;
  color: rgb(6, 31, 253);
}

.container .contactForm .formBox .inputBox input[type="submit"] {
  position: relative;
  cursor: pointer;
  background: skyblue;
  border-radius: 20px;
  color: #fff;
  border: none;
  max-width: 150px;
  padding: 12px;
}

.container .contactForm .formBox .inputBox input[type="submit"]:hover {
  background: black;
  color: white;
  box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.7),
    inset 0px 10px 15px rgba(255, 255, 255, 0.7);
}

@media (max-width: 1200px) {
  .container {
    flex-direction: column;
    align-items: center;
  }

  .container .contactInfo {
    width: 60%;
    margin-bottom: 20px;
  }

  .container .contactForm {
    margin-left: 0;
    width: 70%;
  }

  .container .contactForm .formBox .inputBox.w50 {
    width: 40%;
  }
}

@media (max-width: 768px) {
  .section8 {
    padding: 5rem 1rem;
  }

  .container .contactInfo h2,
  .container .contactForm h2 {
    font-size: 2rem;
  }

  .container .contactInfo ul.info li,
  .container .contactInfo ul.sci li a {
    font-size: 1rem;
  }

  .container .contactForm .formBox .inputBox input,
  .container .contactForm .formBox .inputBox textarea {
    font-size: 16px;
  }

  .container .contactForm .formBox .inputBox span {
    font-size: 16px;
  }

  .container .contactForm .formBox .inputBox input[type="submit"] {
    max-width: 100%;
  }
}

@media (max-width: 480px) {
  .container .contactInfo {
    padding: 0 4rem;
  }

  .container .contactInfo h2 {
    margin: 1rem 0;
    font-size: 1.4rem;
  }

  .container .contactInfo .info img {
    width: 70%;
  }

  .container .contactInfo span,
  a {
    font-size: 0.9rem;
    text-align: justify;
  }

  .container .contactForm {
    padding: 0 4rem;
  }

  .container .contactForm h2 {
    font-size: 1.4rem;
  }

  .container .contactForm .formBox {
    padding-top: 0;
  }

  .container .contactForm .formBox .inputBox span {
    font-size: 0.8rem;
  }

  .container .contactForm .formBox .inputBox textarea {
    height: 50px;
  }
}
