.section3_container {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 3.75rem 10rem;
  margin: 0;
  gap: 1rem;
}

.about__image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about__image img {
  max-width: 28.125rem;
  width: 100%;
  object-fit: cover;
}

.about__content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
}

.section3__subheader {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  margin: 0;
}

.section3__subheader h2 {
  margin: 0;
}

.section3__header {
  margin: 0;
}

.section3__description {
  margin: 0;
  text-align: justify;
}

.address {
  max-width: 550px;
  margin: 0;
}
.address a {
  text-decoration: none;
}

.about__btn {
  margin: 0;
}

.about__btn button {
  padding: 0.8rem 0.8rem;
  font-size: 1rem;
  color: rgb(226, 211, 211);
  background-color: rgb(19, 89, 240);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.about__btn button:hover {
  background-color: darkblue;
}

@media (max-width: 1024px) {
  .section3_container {
    flex-direction: column;
    padding: 2rem 1rem;
  }

  .about__image img {
    max-width: 100%;
  }

  .section3__subheader {
    font-size: 2.5rem;
  }

  .about__btn button {
    font-size: 1rem;
    padding: 0.75rem 1.5rem;
  }
}

@media (max-width: 768px) {
  .section3_container {
    padding: 1.5rem 2rem;
  }

  .section3__header {
    margin: 0;
  }

  .section3__subheader {
    font-size: 2rem;
  }

  .section3__description {
    text-align: justify;
  }

  p {
    margin: 0;
  }

  h4 {
    max-width: 100%;
    font-size: 1rem;
  }

  .about__btn button {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
  }
}

@media (max-width: 480px) {
  .section3_container {
    align-items: center;
    padding: 1rem;
  }

  .section3__subheader h2 {
    font-size: 1rem;
  }

  .section3__header {
    margin: 0;
    font-size: 1rem;
    text-align: center;
  }

  .about__image {
    width: 60%;
  }

  .about__content {
    align-items: center;
    gap: 0.3rem;
  }

  .address {
    align-self: flex-start;
  }

  .about__btn {
    align-self: flex-start;
  }

  .about__content p a {
    font-size: 0.7rem;
  }

  .section3__description {
    text-align: justify;
    width: 70%;
    border-radius: 10px;
    box-shadow: 0 10px 10px rgba(75, 189, 174, 0.7),
      inset 0px 0px 100px 0px rgb(32, 169, 173, 0.2);
    padding: 1rem;
    font-size: 0.7rem;
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 2px solid rgba(13, 188, 211, 0.445);
  }
}
