header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("../assets/ayodhya.webp");
  background-size: cover;
  margin: 0 6.5rem;
  position: sticky;
}

nav {
  display: flex;
  padding: 1rem 1rem;
  justify-content: space-around;
  align-items: center;
  margin: 0rem 1rem;
  width: 70rem;
  background: rgba(16, 30, 189, 0.31);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(3.4px);
  -webkit-backdrop-filter: blur(3.1px);
  border: 1px solid rgba(16, 30, 189, 0.25);
  border-radius: 0rem 0rem 5rem 5rem;
}

.logo a {
  text-decoration: none;
  color: white;
}

.logo span {
  display: block;
}

.nav_links ul {
  display: flex;
  gap: 2rem;
  list-style-type: none;
  padding: 0;
}

.nav_links ul li a {
  text-decoration: none;
  color: white;
  position: relative;
  overflow: hidden;
}

.nav_links a::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background-color: skyblue;
  position: absolute;
  left: 0;
  transform: scaleX(0);
  transition: transform 0.5s ease;
}

.nav_links a:hover::after {
  transform: scaleX(1);
}

.nav_btn button {
  color: black;
  background-color: skyblue;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  outline: none;
  border-radius: 5px;
  border-style: none;
  padding: 0.75rem 1.5rem;
}

@media (max-width: 480px) {
  header {
    width: 100%;
    padding: 0;
    margin: 0;
  }

  nav {
    padding: 0.2rem;
    margin: 0;
  }

  .logo span {
    display: inline;
    color: rgb(240, 6, 6);
    font-weight: bold;
  }

  .nav_links {
    display: none;
  }

  .nav_btn {
    display: none;
  }
}
